body {
  min-height: 100vh;
  background: rgb(220,231,0);
  background: linear-gradient(342deg, rgba(220,231,0,1) 0%, rgba(251,248,0,1) 50%, rgba(218,0,0,1) 100%);
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 50%;
  border: none;
  padding: 10px;
}
.search-button{
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}